import axios from 'axios'
const baseUrl = '/api/ministry-job-openings/'
import store from '../store'

const getLanguage = () => {
  const language = store.getState().language
  return language
}

const getMinistryJobOpenings = async () => {
  const response = await axios.get(`${baseUrl}${getLanguage()}`)
  return response.data
}

const createMinistryJobOpening = async (newItem) => {
  const token = `bearer ${JSON.parse(localStorage.loggedInAdminUser).token}`
  const config = {
    headers: { Authorization: token },
  }

  const response = await axios.post(`${baseUrl}${getLanguage()}`, newItem, config)

  return response.data
}

const updateMinistryJobOpening = async (id, updatedItem) => {
  const token = `bearer ${JSON.parse(localStorage.loggedInAdminUser).token}`
  const config = {
    headers: { Authorization: token }
  }

  const response = await axios.put(`${baseUrl}${id}/${getLanguage()}`, updatedItem, config)

  return response.data
}

const deleteMinistryJobOpening = async (id) => {
  const token = `bearer ${JSON.parse(localStorage.loggedInAdminUser).token}`
  const config = {
    headers: { Authorization: token }
  }

  const response = await axios.delete(`${baseUrl}/${id}`, config)

  return response.data
}

const ministryJobOpeningsService = {
  getMinistryJobOpenings,
  createMinistryJobOpening,
  updateMinistryJobOpening,
  deleteMinistryJobOpening,
}

export default ministryJobOpeningsService
